@import url('https://fonts.googleapis.com/css2?family=Lato&family=Poppins&display=swap');

.Contact-text {
  position: relative;
  width: 100%;
  max-width: 500;
  margin: 0px 50px 0px 95px;
  text-align: left;

  .title {
    font-family: 'Poppins', sans-serif;
    color: #3f005e;
    font-weight: 800;
    font-size: 48px;
  }

  .title-section-infos,
  .name-advisor,
  .email-advisor {
    color: black;
  }

  .section-icons-contact{
    display: flex;
    flex-direction: column;
    gap: 20px;
    margin: 0;
    padding: 40px 0 50px;
  }

  .icons-contact-collum1,
  .icons-contact-collum2 {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 30px;
  }

  .icone1-contact,
  .icone2-contact,
  .advisor-icons1,
  .advisor-icons2,
  .advisor-icons3 {
    background-color: #3f005e;
    height: 55px;
    width: 55px;
    border-radius: 50%;
  }

  .section-advisor-infos{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 34%;
    width: 40%;

    .title-section-infos{
      font-size: 1.7em;
      color: #3f005e;
      font-weight: bolder;
      font-family: 'Poppins', sans-serif;
      margin-bottom: 10px;
    }

    .name-advisor,
    .email-advisor {
      margin: 0;
    }

    .advisor-icons-contact{
      display: flex;
      gap: 20px;
      margin: 0;
      padding: 40px 0;
    }
  }

  .section-maps-content{
    background-image: url('../../assets/maps.png');
    position: absolute;
    right: 10%;
    top: 10%;
    height: 380px;
    width: 450px;
    border-radius: 20px;
  }

}